/* eslint-disable no-shadow */
import React from 'react';
import { Formik } from 'formik';
import * as yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import BirthDay from '../../common/Form/BirthDayInput';
import { GetUserInfo } from '../../../store/userUnfo/selectors';
import Form from '../../common/Form';
import Input from '../../common/Form/Input';
import ButtonPrimary from '../../common/Button/ButtonPrimary';
import InputPassword from '../../common/Form/InputPassword';
import 'react-datepicker/dist/react-datepicker.css';
import makeRequest from '../../../network';
import { RegisrationUserAction } from '../../../store/userUnfo/actions';

type InfoUserType = {
  name: string;
  lastName: string;
  middleName: string;
  email: string;
  birthDay: any;
  phone: string;
  gender: string;
  password: string;
  repeatPassword: string;
};

const ProfileEditPage = () => {
  const infoUser = useSelector(GetUserInfo);
  const dispatch = useDispatch();
  const initialValues: InfoUserType = {
    name: infoUser.name,
    lastName: '' || infoUser.lastName,
    middleName: '' || infoUser.middleName,
    email: infoUser.email,
    phone: infoUser.phone,
    gender: infoUser.gender,
    password: '',
    repeatPassword: '',
    birthDay: infoUser.birthDay ? new Date(infoUser.birthDay) : null,
  };
  const phoneRegExp = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/;
  const validationSchema = yup.object().shape({
    name: yup
      .string()
      .typeError('  ')
      .min(2, '    ')
      .required(' '),
    email: yup.string().email(' ').required(' '),
    phone: yup
      .string()
      .matches(phoneRegExp, '   +79001432425  89001432425')
      .required(' '),
    lastName: yup
      .string()
      .typeError('  ')
      .min(2, '    '),
    middlename: yup
      .string()
      .typeError('  ')
      .min(2, '    '),
    password: yup
      .string()
      .typeError('  ')
      .oneOf([yup.ref('repeatPassword')], '  ')
      .required(' '),
    repeatPassword: yup
      .string()
      .oneOf([yup.ref('password')], '  ')
      .required(' '),
  });
  return (
    <Formik
      initialValues={initialValues}
      validateOnBlur
      onSubmit={async (values) => {
        const { repeatPassword, ...data } = values;
        const response = await makeRequest({
          url: '/profile/edit',
          method: 'PUT',
          data,
          headers: { authorization: `Bearer ${infoUser.token}` },
        });
        dispatch(RegisrationUserAction(response));
      }}
      validationSchema={validationSchema}>
      {({
        handleSubmit,
        values,
        handleChange,
        handleBlur,
        touched,
        isValid,
        dirty,
        errors,
        setFieldValue,
      }) => (
        <Form handleSubmit={handleSubmit} textClass="edit">
          <Input
            id="name"
            textClass="edit"
            placeholder=" "
            setValue={handleChange}
            handleBlur={handleBlur}
            value={values.name}
            text=""
            err={touched.name && errors.name}
          />
          <Input
            id="lastName"
            textClass="edit"
            placeholder=" "
            setValue={handleChange}
            handleBlur={handleBlur}
            value={values.lastName}
            text=""
            err={touched.lastName && errors.lastName}
          />
          <Input
            id="middleName"
            placeholder=" "
            textClass="edit"
            setValue={handleChange}
            handleBlur={handleBlur}
            value={values.middleName}
            text=""
            err={touched.middleName && errors.middleName}
          />
          <Input
            text="email"
            textClass="edit"
            id="email"
            placeholder=" email"
            setValue={handleChange}
            handleBlur={handleBlur}
            value={values.email}
            err={touched.email && errors.email}
          />
          <BirthDay
            id="birthDay"
            name="birthday"
            value={values.birthDay}
            selected={values.birthDay}
            setValue={setFieldValue}
            dateFormat="dd.MM.yyyy"
          />
          <Input
            id="phone"
            textClass="edit"
            placeholder=" "
            setValue={handleChange}
            handleBlur={handleBlur}
            value={values.phone}
            text=""
            err={touched.phone && errors.phone}
          />
          <InputPassword
            text=""
            textClass="edit"
            id="password"
            placeholder="********"
            handleBlur={handleBlur}
            setValue={handleChange}
            value={values.password}
            err={touched.password && errors.password}
          />
          <InputPassword
            text=" "
            textClass="edit"
            id="repeatPassword"
            placeholder="********"
            handleBlur={handleBlur}
            setValue={handleChange}
            value={values.repeatPassword}
            err={touched.repeatPassword && errors.repeatPassword}
          />
          <ButtonPrimary title="" type="submit" disabled={!isValid && !dirty} />
        </Form>
      )}
    </Formik>
  );
};

export default ProfileEditPage;
